import React from 'react'
import SidebarOptions from './SidebarOptions';
import StorefrontIcon from "@mui/icons-material/Storefront";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import GroupIcon from "@mui/icons-material/Group";
import Diversity3Icon from "@mui/icons-material/Diversity3";
import EventIcon from "@mui/icons-material/Event";
import { Paper } from '@mui/material';



const Sidebar = () => {
  return (
    <>
      <section className='hidden md:block mt-14'>
        <Paper elevation={3}>
          <div className="flex  bg-white h-screen flex-col sticky top-0 overflow-y-auto ">
            <SidebarOptions
              src={
                "https://i.pinimg.com/564x/46/5e/d1/465ed15a62706700067ce05878650579.jpg"
              }
              title="Ashish Soni"
            />
            <SidebarOptions
              Icon={StorefrontIcon}
              // src="https://www.pngitem.com/pimgs/m/395-3953339_socialdealer-offers-facebook-marketplace-for-automotive-circle-hd.png"
              title="Marketplace "
            />
            <SidebarOptions
              Icon={GroupIcon}
              // src="data:image/jpeg;base64,"
              title="Friends"
            />
            <SidebarOptions
              Icon={Diversity3Icon}
              // src="data:image/png;base64,"
              title="Groups"
            />
            <SidebarOptions
              Icon={EventIcon}
              // src="data:image/jpeg;base64,"
              title="Event"
            />
            <SidebarOptions Icon={ExpandMoreIcon} title="See More" />
          </div>
        </Paper>
      </section>
    </>
  );
}

export default Sidebar